@import "sass/helpers/variables";
/* Start Hero Image */
.homepage-hero {
  @media #{$mobile} {
    text-align: center;
  }

  &-content {
    .hero-img-title {
      @media #{$mobile} {
        font-size: 36px;
        line-height: 44px;
        width: 85%;
        margin: 20px auto 17px;
      }
    }
    .hero-button {
      > span {
        @media #{$mobile} {
          i {
            font-size: 10px;
          }
        }
      }
    }
    .hero-button {
      @media #{$mobile} {
        width: 100%;
        display: block;
      }
    }

    .hero-img-btn {
      @media #{$mobile} {
        margin-top: 30px;
        width: 100%;
        padding: 15px;
        display: block;
        margin-bottom: 10px;
      }
    }
    .version {
      @media #{$mobile} {
        margin-top: 10px;
      }
    }
  }

  @media #{$mobile} {
    padding: 60px 0;
    background-image: linear-gradient(to right, rgba(9, 32, 46, 0.7), rgba(0, 0, 0, 0.5)), url(../imgs/homepage/homepage-mobile.webp);
  }

  .hero-img-txt {
    @media #{$mobile} {
      font-size: 18px;
      line-height: 30px;
    }
  }
  .title {
    @media #{$mobile} {
      font-size: 26px;
    }
  }
  .hero-img-title {
    @media #{$mobile} {
      font-size: 40px;
      line-height: 50px;
      margin-bottom: 20px;
      width: 100%;
    }
  }

  .trustpilot_img {
    @media #{$mobile} {
      margin: 30px 0;
      img {
        height: 22px;
      }
    }
  }
  .hero-img-content {
    .homepage-2-button {
      .hero-img-btn {
        @media #{$mobile} {
          margin: 0;
          min-height: 64px;
        }
      }
    }
  }
}
/* End Hero Image */

// Start Homepage Testimonials
.homepage-testimonials {
  @media #{$mobile} {
    padding: 0;
  }
  .testimonial-content {
    @media #{$mobile} {
      background: #ffffff;
      padding: 20px 0;
    }
  }

  .testimonial {
    @media #{$mobile} {
      margin: 0 14px;
      padding: 20px 0;
    }

    &.one {
      @media #{$mobile} {
        border-bottom: 1px solid #ffebf0;
      }
      &:after {
        @media #{$mobile} {
          display: none;
        }
      }
    }
    .text {
      .author {
        .quote {
          @media #{$mobile} {
            right: 10px;
          }
        }
      }
    }
  }
}

.secure_servers {
  &.homepage_servers {
    .secure_servers_content {
      @media #{$mobile} {
        padding: 200px 0 60px;
      }
    }
  }
  .left_col {
    @media #{$mobile} {
      overflow: auto;
      padding: 0 20px;
    }
  }
  .container {
    @media #{$mobile} {
      max-width: 100%;
      padding: 0 15px;
      .padd_xs_0 {
        padding: 0;
      }
    }
  }
  .secure_servers_content {
    @media #{$mobile} {
      padding: 60px 0;
      background: none;
    }
  }
  .servers_countries {
    @media #{$mobile} {
      width: 1400px;
      justify-content: flex-start;
      &.lazyloaded {
        background: center / contain no-repeat url(../imgs/homepage/map-servers-mobile.svg);
      }
    }
  }
  .secure_servers_txt {
    h2 {
      @media #{$mobile} {
        padding: 0 17px;
        margin-top: 40px;
      }
    }
    .server-btn {
      a {
        @media #{$mobile} {
          max-width: unset;
        }
      }
    }
  }
}

.unblock-content {
  @media #{$mobile} {
    padding: 60px 0;
    text-align: center;
    .image {
      img {
        margin-bottom: 40px;
      }
    }
  }
}

.anonymous {
  @media #{$mobile} {
    padding: 60px 0;
    text-align: center;
    .image {
      img {
        margin-bottom: 40px;
      }
    }
  }
}

// ================

.wevpn-feats {
  .advantage {
    @media #{$mobile} {
      margin-bottom: 50px;
      &.last {
        margin-bottom: 0;
      }
    }
  }
  .advantage-text {
    &:after {
      @media #{$mobile} {
        display: none;
      }
    }
    h4 {
      @media #{$mobile} {
        font-size: 18px;
        margin: 35px 0 18px;
      }
    }
    p {
      @media #{$mobile} {
        width: 90%;
      }
    }
  }
}

.wevpn_communities {
  .content {
    @media #{$mobile} {
      top: -135px;
    }
  }
  .widget {
    @media #{$mobile} {
      margin-bottom: 20px;
      filter: drop-shadow(0px 4px 9px rgba(0, 0, 0, 0.15));
    }
    &__text {
      @media #{$mobile} {
        font-size: 14px;
        padding: 19px 15px;
      }
    }
    &__link {
      a {
        @media #{$mobile} {
          height: 44px;
          line-height: 44px;
        }
      }
    }
  }
}

// ============================================
// Common Sections

.wevpn-devices {
  @media #{$mobile} {
    padding: 65px 0 185px;
    text-align: center;
    background: linear-gradient(154.48deg, #fe6556 -9.73%, #fd3e72 66.22%, #49a8ff 163.3%);
  }
}

.wevpn-ethics {
  @media #{$mobile} {
    padding: 60px 0 35px;
  }
  h2 {
    @media #{$mobile} {
      font-size: 24px;
      line-height: 34px;
    }
  }
  .ethics-text {
    @media #{$mobile} {
      font-size: 16px;
      line-height: 20px;
    }
  }

  .box {
    @media #{$mobile} {
      margin-bottom: 25px;
      padding: 25px 20px;
    }
  }
}

.home-trustbox {
  iframe {
    @media #{$mobile} {
      height: 140px !important;
    }
  }
}

// Start Wevpn Features
.wevpn-features {
  &:after {
    @media #{$mobile} {
      display: none;
    }
  }

  .support {
    @media (max-width: 1050px) {
      padding: 0 30px;
    }
    h3 {
      @media (max-width: 550px) {
        max-width: 320px;
        margin: auto;
      }
    }
  }
  .money-back {
    @media #{$mobile} {
      background: linear-gradient(154.48deg, #fe6556 -9.73%, #fd3e72 66.22%, #49a8ff 163.3%);
    }
    .text {
      @media #{$mobile} {
        max-width: 430px;
      }
    }
  }
}
